.profileImage {
    width: 150px !important;
    height: 150px !important;
    border-radius: 50%;
}
.graduation_section {
    display: flex;
}
.profileImageLabel {
    margin: 0;
    position: relative;

    >i {
        position: absolute;
        bottom: -3px;
        right: -3px;
    }
}

.pprofile1 {
    padding: 21px 22px;
    width: 100%;
    margin-right: auto;
    margin-left: auto;
    background-color: #fff;
    border-radius: 12px;
}

p.bg-light.rounded.py-2.fieldcls {
    background-color: #775DA62b !important;

}

.ViewUser {
    // font-weight: 500;
    color: #202020;
    font-weight: 600;
    font-size: 36px;
    margin-bottom: 32px;
}

// change page design
.change_section {
    width: 100%;
    max-width: 600px;
    margin: auto;
    padding: 2rem;
    background: #edfaff;
    border-radius: 25px;
    box-shadow: 3px 1px 13px #bfbfbf;
  }
  .profile_img_side {
    border: 1px solid #eeeeee;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    border-radius: 10px;
    box-shadow: 0px 0px 12px #e2e2e2;
    margin-bottom: 2rem;
  }
  a.profiles_edit {
    height: 30px;
    width: 30px;
    background: #006fb0;
    display: flex;
    align-items: center;
    justify-content: center;
    border-radius: 50%;
    font-size: 13px;
    color: #fff;
    position: relative;
    bottom: -54px;
    left: -50px;
    &:hover{
        text-decoration: none;
    }
}
i.material-icons.prob_new {
    font-size: 17px;
    margin: 0px 7px;
    color: #ffffff;
}

.right_profile {
    border: 1px solid #eeeeee;
padding: 2rem;
    height:auto;
    border-radius: 10px;
    box-shadow: 0px 0px 12px #e2e2e2;
}
.label_profile {
    font-size: 18px;
    font-weight: 500 !important;
    text-transform: capitalize;
    color: #202020;
}
p.profile_data {
    margin-bottom: 0px !important;
    font-size: 14px;
    font-weight: 400;
    color: #5A5A5A;
    background: #f0f0f0 !important;
    padding: 10px;
    border-radius: 10px;
}
.education_name {
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 13px;
    border: 1px solid #f0f0f0;
    padding: 10px;
    border-radius: 10px;
}
.edit_studies {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: auto;
}
i.material-icons.new_icons {
    color: #0073ac;
    cursor: pointer;
    font-size: 19px;
}
h5.education_heading {
    font-weight: 600;
    font-size: 16px;
    margin-bottom: 0px !important;
}
p.about_school {
    margin: 4px 0px 2px 0px;
    font-size: 14px;
    font-weight: 400;
    color: #3d3d3d;
}
p.about_school_year {
    font-size: 13px;
    color: gray;
    margin-bottom: 0px;
}
.add_icon {
    border: 1px solid #d7d7d7;
    padding: 3px;
    margin: 0px 6px;
    display: flex;
    align-items: center;
    border-radius: 50%;
    width: 26px;
    height: 26px;
    margin-left: auto;
}
.headings {
    display: flex;
}